.c-file-Image {
  position: relative;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  -webkit-backface-visibility: hidden;
  box-shadow: 3px 3px 3px lightgray;
}
.c-file-Image.animate {
  animation: c-file-Image-FadeIn 0.4s;
}
@keyframes c-file-Image-FadeIn {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  60% {
    opacity: 1;
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
