.c-file .link {
  top: 0px;
  position: absolute;
  z-index: 10;
  background-color: white;
  padding: 1px 5px;
  border-radius: 3px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.c-file .link.addLink,
.c-file .link.deleteLink {
  right: 0px;
}
.c-file .link.addLink:hover,
.c-file .link.deleteLink:hover {
  background-color: lightblue;
}
.c-file .link.downloadLink {
  right: 26px;
}
.c-file .link.downloadLink:hover {
  background-color: lightblue;
}
.c-file .link.previewLink {
  right: 52px;
}
.c-file .link.previewLink:hover {
  background-color: lightblue;
}
