.c-file-Pdf {
  position: relative;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  -webkit-backface-visibility: hidden;
}
.c-file-Pdf.animate {
  animation: a-pdf-FadeIn 0.4s;
}
.c-file-Pdf .page {
  border: none;
  box-shadow: 3px 3px 3px lightgray;
}
.c-file-Pdf .page.paper {
  background: linear-gradient(-45deg, #ffffff, #ffffff), linear-gradient(45deg, #ffffff, #ffffff), linear-gradient(135deg, transparent 7%, #ffffff 7%);
  background-size: 50% 0%, 50% 100%, 100% 100%;
  background-position: 0 0, 100% 100%, 100% 0;
  background-repeat: no-repeat;
}
.c-file-Pdf .page.paper:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 16%;
  height: 11%;
  box-shadow: 1px 1px 1px rgba(128, 128, 128, 0.3), 2px 2px 2px rgba(128, 128, 128, 0.2);
  background: linear-gradient(135deg, transparent 50%, #ffffff 50%);
}
.c-file-Pdf .page .react-pdf__Page__svg {
  background: transparent !important;
}
.c-file-Pdf *,
.c-file-Pdf :after,
.c-file-Pdf :before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.c-file-Pdf .annotationLayer {
  display: none;
}
.c-file-Pdf .previousPage {
  left: 0px;
}
.c-file-Pdf .nextPage {
  right: 0px;
}
.c-file-Pdf .previousPage,
.c-file-Pdf .nextPage {
  text-decoration: none;
  font-size: 1.2rem;
  position: absolute;
  top: 45%;
  z-index: 2;
  -webkit-text-stroke: 2px white;
}
.c-file-Pdf .previousPage:hover,
.c-file-Pdf .nextPage:hover {
  text-decoration: none;
}
.c-file-Pdf .pageNumber {
  position: absolute;
  bottom: 0px;
  right: 0px;
  z-index: 2;
  background-color: white;
  padding: 0px 3px;
}
@keyframes c-Editor-FadeIn {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  60% {
    opacity: 1;
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
